.nav-container{
    background-color: #F6F6F6;
    padding: 22.5px 0;

    &__header-nav {
        display: flex;
        max-height: 90px;
        align-items: center;
        justify-content: space-between;
    }

    &__nav-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;

    }

    &__header-nav span {
        width: 41px;
        height: 14px;
        padding: 0 7px;
        opacity: .5;
    }

    &__login {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__userLogo {
        height: 18px;
        width: 18px;
        opacity: .5;
        padding: 7px 0 0;
    }
    &__user-button{
        cursor: pointer;
        background:none;
        border: none;
        transition-duration: 1.5s;
    }

    &__user-button:hover{
        color: green;
        transition-duration: 1.5s;
    }

    &__button {
        background-color: #1234E8;
        border: none;
        border-radius: 5px;
        color: #FFFFFF;
        padding: 16px 30px 15px;
        font-family: Montserrat;
        font-style: SemiBold;
        font-size: 14px;
        line-height: 14px;
    }

}